<template>
  <div class="titlebarwraper">
    <div class="titlebar" @mousedown.left="setmoveflag" @mouseup.left="canclemoveflag" @mousemove="movewindow">

    </div>

    <div class="righticons">

      <el-icon color="black" class="no-inherit" style="margin-right: 5px">
        <minus @click="minuswindow"/>
      </el-icon>
      <el-icon color="black" class="no-inherit">
        <close @click="closewindow"/>
      </el-icon>
    </div>
  </div>
</template>
<style>
.righticons{
  float: right;
}
.el-icon{
  cursor: pointer;
}
.titlebarwraper{
  display: flex;

}
.titlebar{
  flex:1;
  height: 40px;
background-color: blue;
  -webkit-user-select: none;
  -webkit-app-region: drag;
}
</style>
<script setup>

function minuswindow(){
  window.myapi.minuswindow()
}
function closewindow(){
  window.myapi.closewindow()
}
// eslint-disable-next-line
let flag=0;
let oldposition={x:0,y:0}
function setmoveflag(e){
  flag=1;
  oldposition={x:e.clientX,y:e.clientY}
}
function canclemoveflag(){
  flag=0;
}
function movewindow(e){
  console.log(e)
  if (flag){

    let tmp={x:e.clientX-oldposition.x,y:e.clientY-oldposition.y}
    oldposition={x:e.clientX,y:e.clientY}
    window.movewindow(window.screenLeft+tmp.x,window.screenTop+tmp.y,window.outerWidth,window.outerHeight)
  }
}
</script>


