
import { createStore } from 'vuex'

// Create a new store instance.
const store = createStore({
    state () {
        return {
            token: ''
        }
    },
    mutations: {
        setToken (state,token) {
            state.token=token
        }
    },
    getters: {
        token: (state) => {
            return state.token
        }
}})
export default store
